<template>
    <v-card>
        <div v-if="$route.path !== '/calendar'">
            <h2>행사 정보</h2>

            <ul>
                <li v-for="(event, index) in showevents" :key="event.id">
                    <br>
                    <div style="display: flex; align-items: center;">
                        <div>
                            <!-- <img :src="event.firstimage" alt="축제 이미지" style="width:200px; height: 200px;"> -->

                            <img v-if="event.firstimage !== undefined && event.firstimage !== null && event.firstimage !== ''"
                                :src="event.firstimage" alt="이미지 존재" style="width: 200px; height: 200px;" @click="handleImageClick(event)" />
                            <img v-else src="https://via.placeholder.com/200x200.png?text=No+Image" alt="이미지가 존재하지않습니다."
                                style="width: 200px; height: 200px;" />
                        </div>
                        <div style="margin-left: 10px; text-align: left;">
                            <div>
                                <h3> {{ event.title }}</h3>
                            </div>
                            <div>주소1: {{ event.addr1 }}</div>
                            <div>축제 기간: {{ event.eventstartdate }} - {{ event.eventenddate }}</div>
                            <div>
                                <span v-if="homepage[index]">
                                    홈페이지: <span v-html="homepage[index]"></span>
                                </span>
                                <span v-else>
                                    홈페이지가 없습니다.
                                </span>
                            </div>
                        </div>
                    </div>
                    <hr>
                    <br>
                </li>
            </ul>
            <v-pagination v-model="currentPage" :length="totalPages"></v-pagination>
            <!-- <v-btn @click="loadMore" v-if="hasMoreData">더 많은 행사 가져오기</v-btn> -->
        </div>
    </v-card>
</template>
  
<script>

export default {
    data() {
        return {
            /*
            events: [{
                addr1: '',
                eventstartdate: 0,
                eventenddate: 0,
                firstimage: '',
                mapx: 0,
                mapy: 0,
                title: '',
            }],*/

            hasMoreData: true,
            showevents: [], //보여질 이벤트 데이터
            events: [], // 전체 이벤트 데이터
            currentPage: 1,
            homepage: [],
            selectedItem: 1,
            showContent: false,

            itemsPerPage: 5,  //한페이지에 보여줄 갯수

        };
    },
    watch: {

        currentPage() {
            this.updateDisplayedEvents();
            console.log('꿈?');
        },
    },
    computed: {
        // 전체 페이지 수 계산
        totalPages() {
            return Math.ceil(this.events.length / this.itemsPerPage);
        },
    },

    methods: {
        async getselectedItem(setSelectedItem) {
            try {

                const selItem = this.selectedItem !== setSelectedItem;

                if (selItem) {
                    this.selectedItem = parseInt(setSelectedItem);
                    this.homepage = [];
                    this.events = []; // 키워드가 변경된 경우, 이전 데이터를 초기화
                    this.currentPage = 1; // 페이지를 다시 1로 설정
                    this.hasMoreData = true; // 새로운 키워드로 검색하므로 더 많은 데이터가 있을 수 있음

                }
                // 현재 날짜의 Date 객체 생성
                const currentDate = new Date();

                // 현재 달의 첫째 날 계산
                const firstDayOfMonth = new Date(currentDate.getFullYear(), currentDate.getMonth(), 1);

                // 현재 달의 마지막 날 계산
                const lastDayOfMonth = new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 0);

                // 첫째 날과 마지막 날의 날짜를 YYYYMMDD 형식의 문자열로 변환
                const formattedFirstDay = formatDate(firstDayOfMonth);
                const formattedLastDay = formatDate(lastDayOfMonth);
                console.log("시작일 마감일 : " + formattedFirstDay + "-" + formattedLastDay);

                function formatDate(date) {
                    const year = date.getFullYear();
                    const month = (date.getMonth() + 1).toString().padStart(2, '0');
                    const day = date.getDate().toString().padStart(2, '0');
                    return `${year}${month}${day}`;
                }

                //달력페이지가 열려있다면
                if ("$route.path == '/calendar'") {
                    let serviceKey = 'bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D'
                    const response = await this.axios.get(`http://apis.data.go.kr/B551011/KorService1/searchFestival1?MobileOS=WIN&MobileApp=WebTest&numOfRows=300&eventStartDate=${formattedFirstDay}&eventEndDate=${formattedLastDay}&areaCode=${this.selectedItem}&_type=json&serviceKey=${serviceKey}`);
                    // 아빠 인증키 NNMlfJvtCxCDfwt0PP4Nebtul2vSAhEjQEhMTNHBWLDYCOppGMmuLGA9EvdoZmY7BoyIlCGw96tjiko848Zntw%3D%3D
                    // 내 인증키 bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D
                    const newEvents = response.data.response.body.items.item;
                    //const EventArray = newEvents.map(event => ({ addr1: event.addr1, eventstartdate: event.eventstartdate, eventenddate: event.eventenddate, firstimage: event.firstimage, mapx: event.mapx, mapy: event.mapy, title: event.title }));
                    // 각 이벤트의 contentid를 추출하여 배열 생성
                    if (newEvents && newEvents.length > 0) {

                        this.events = this.events.concat(newEvents); // 새로운 데이터를 기존 데이터에 추가
                    }
                    this.$bus.$emit('emptyEvent', this.events);
                    this.updateDisplayedEvents();

                } 
                else {
                    let serviceKey = 'bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D'
                    const response = await this.axios.get(`http://apis.data.go.kr/B551011/KorService1/searchFestival1?MobileOS=WIN&MobileApp=WebTest&pageNo=${this.currentPage}&eventStartDate=${formattedFirstDay}&eventEndDate=${formattedLastDay}&areaCode=${this.selectedItem}&_type=json&serviceKey=${serviceKey}`);
                    // 아빠 인증키 NNMlfJvtCxCDfwt0PP4Nebtul2vSAhEjQEhMTNHBWLDYCOppGMmuLGA9EvdoZmY7BoyIlCGw96tjiko848Zntw%3D%3D
                    // 내 인증키 bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D
                    const newEvents = response.data.response.body.items.item;

                    if (newEvents && newEvents.length > 0) {
                        const contentIds = newEvents.map(event => event.contentid);

                        this.$bus.$emit('contentid', contentIds); // eventHomepage뷰로 contentid 전달
                        //console.log(contentIds)
                        this.events = this.events.concat(newEvents); // 새로운 데이터를 기존 데이터에 추가


                        console.log(this.events);

                        this.currentPage++;
                    } else {
                        this.hasMoreData = false;
                    }
                }


            } catch (error) {
                console.error('Error fetching events:', error);
            }


        },
        eventData(getEventData) {
            const homepages = getEventData.map(item => item.homepage);
            this.homepage = this.homepage.concat(homepages);
            console.log(this.homepage);

        },
        // loadMore() {
        //     if (this.hasMoreData) {
        //         this.events = [];
        //         this.contentIds = [];
        //         console.log("버튼을 눌림, seleteditem:" + this.selectedItem);
        //         this.getselectedItem(this.selectedItem);
        //     }
        // },

        async handleCalendarDateChange(startDate, endDate) {
            function formatDateToYYYYMMDD(dateString) {
                const date = new Date(dateString);
                const year = date.getFullYear();
                const month = (date.getMonth() + 1).toString().padStart(2, '0');
                const day = date.getDate().toString().padStart(2, '0');
                return `${year}${month}${day}`;
            }

            const formattedStartDate = formatDateToYYYYMMDD(startDate);
            const formattedEndDate = formatDateToYYYYMMDD(endDate);
            console.log(formattedStartDate);

            if ("$route.path == '/calendar'") {
                let serviceKey = 'bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D'
                const response = await this.axios.get(`http://apis.data.go.kr/B551011/KorService1/searchFestival1?MobileOS=WIN&MobileApp=WebTest&numOfRows=300&eventStartDate=${formattedStartDate}&eventEndDate=${formattedEndDate}&areaCode=${this.selectedItem}&_type=json&serviceKey=${serviceKey}`);
                // 아빠 인증키 NNMlfJvtCxCDfwt0PP4Nebtul2vSAhEjQEhMTNHBWLDYCOppGMmuLGA9EvdoZmY7BoyIlCGw96tjiko848Zntw%3D%3D
                // 내 인증키 bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D
                const newEvents = response.data.response.body.items.item;
                //const EventArray = newEvents.map(event => ({ addr1: event.addr1, eventstartdate: event.eventstartdate, eventenddate: event.eventenddate, firstimage: event.firstimage, mapx: event.mapx, mapy: event.mapy, title: event.title }));
                // 각 이벤트의 contentid를 추출하여 배열 생성
                if (newEvents && newEvents.length > 0) {
                    //const contentIds = newEvents.map(event => event.contentid);
                    //this.$bus.$emit('contentid', contentIds); // eventHomepage뷰로 contentid 전달
                    //console.log(contentIds)
                    this.events = this.events.concat(newEvents); // 새로운 데이터를 기존 데이터에 추가
                }
                this.$bus.$emit('emptyEvent', this.events);
                console.log("이벤트API에서 보내줄 데이터:")
                console.log(this.events);
                this.events = [];
            }
        },
        // 현재 페이지에 따라 표시할 이벤트를 업데이트합니다.
        updateDisplayedEvents() {
            const start = (this.currentPage - 1) * this.itemsPerPage;
            const end = start + this.itemsPerPage;
            this.showevents = this.events.slice(start, end);
        },

        // 선택된 축제 장소가 구글맵에 표시되게 하기 위해 이벤트버스로 주소 전송
        handleImageClick(index) {
            
            this.$bus.$emit('sendEventLocation', index.addr1);
            
        },

    },
    created() {
        // 선택된 지역이 어디인가 받는 이벤트버스 from App.vue
        this.$bus.$on('sendSelectedItem', this.getselectedItem);

        this.$bus.$on('calendarDateChange', this.handleCalendarDateChange);
    },
    // beforeDestroy() {
    //     this.$bus.$off('calendarDateChange', this.handleCalendarDateChange);
    // },
    mounted() {
        this.$bus.$on('sendhomepage', this.eventData);
        //this.$bus.$on('emptyEvent', this.emptyEvent);


        //this.$bus.$on('emptyEvent', this.setSelectedItem); //삭제가능


    },

};
</script>

<style scoped>

li {
    list-style: none;
    /* 리스트 마커를 제거합니다. */
}
</style>