import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import KeywordSearch from '../views/keywordS.vue'
import key_crawling from '../views/key_crawling.vue'
import key_EventDetail from '../views/key_EventDetail.vue'
import eventAPI_homepage from '../views/eventAPI_homepage.vue'
import practice3 from '../views/practice3.vue'
import calendar from '../views/calendar.vue'
import adminpage from '../views/adminpage.vue'
import Login from '../views/Login.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'keyword',
    component: KeywordSearch,

  },
  {
    path: '/keyword',
    name: 'key_Detail',
    component: key_EventDetail
  },
  {
    // 와일드 카드인 *는 위에 경로들 외에 다른 경로로 들어가면 나오는 페이지
    path: '/eventCrawling',
    name: 'crawling',
    component: key_crawling
  },
  {
    path: '/eventAPI',
    name: 'apiHome',
    component: eventAPI_homepage
  },
  {
    path: '/calendar',
    name: 'calendar2',
    component: calendar
  },
  {
    path: '/adminpage',
    name: 'adminpage',
    component: adminpage
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  /*
  {
    path: '/cal',
    name: 'cal',
    component:practice3
  }*/


]



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})




export default router
