<template>
    <v-card>
        <div class="centered-container">
            <h2>키워드를 입력해주세요</h2>
            <ul>
                <li v-for="(event, index) in showevents" :key="index">
                    <br />
                    <div style="display: flex; align-items: center;">
                        <div>
                            <img v-if="event.firstimage !== undefined && event.firstimage !== null" :src="event.firstimage"
                                alt="이미지가 존재" style="width: 200px; height: 200px;" @click="handleImageClick(event)"/>
                            <img v-else src="https://via.placeholder.com/200x200.png?text=No+Image" alt="이미지가 존재하지않습니다."
                                style="width: 200px; height: 200px;" />
                        </div>
                        <div style="margin-left: 10px; text-align: left;">
                            <div>
                                <h3>{{ event.title }}</h3>
                            </div>
                            <div><b>주소</b>: {{ event.addr1 }}</div>

                            <span v-if="eventplace[index]">
                                <b>장소</b>: {{ eventplace[index] }}
                            </span>
                            <span v-else>
                                장소 불러오는중..
                            </span>
                            <br>
                            <span v-if="eventstartdate[index]">
                                <b>축제시간</b>: {{ eventstartdate[index] }}~{{ eventenddate[index] }}
                            </span>
                            <span v-else>
                                시간 불러오는중..
                            </span>
                        </div>
                    </div>
                    <hr />
                    <br />
                </li>
            </ul>
            <v-pagination v-model="currentPage" :length="totalPages"></v-pagination>
            <!-- <v-btn @click="loadMore" v-if="hasMoreData">행사 더보기</v-btn> -->
        </div>
    </v-card>
</template>
  
<script>
export default {
    name: 'event-info3',
    data() {
        return {
            keyword: '',
            events: [],
            currentPage: 1,

            // hasMoreData: true,
            showevents: [],

            itemsPerPage: 5,

            contentIds: [],

            // eventstartdates: [],
            eventstartdate: [],

            // eventenddates: [],
            eventenddate: [],

            // eventplaces: [],
            eventplace: []
        };
    },
    created() {
        // 키워드 받아옴
        this.$bus.$on('sendKeyword', this.handleKeyword);
    },
    beforeDestroy() {
        this.$bus.$off('sendEventDetail', this.eventData);
    },
    mounted() {
        this.$bus.$on('sendEventDetail', this.eventData);


    },
    watch: {

        currentPage() {
            this.updateDisplayedEvents();
            console.log('장바꿈?');
        },

    },
    computed: {

        // 전체 페이지 수 계산
        totalPages() {
            return Math.ceil(this.events.length / this.itemsPerPage);
        },

    },

    methods: {
        // 현재 페이지에 따라 표시할 이벤트를 업데이트합니다.
        updateDisplayedEvents() {
            const start = (this.currentPage - 1) * this.itemsPerPage;
            const end = start + this.itemsPerPage;
            this.showevents = this.events.slice(start, end);
        },

        eventData(getEventData) {
            // 이런형식으로 바꿔보기
            // const homepages = getEventData.map(item => item.homepage);
            // this.homepage = this.homepage.concat(homepages);

            // this.eventstartdates = [];
            this.eventstartdate = [];
            // this.eventenddates = [];
            this.eventenddate = [];
            // this.eventplaces = [];
            this.eventplace = [];

            const eventstartdates = getEventData.map(item => item.eventstartdate);
            this.eventstartdate = this.eventstartdate.concat(eventstartdates);
 
            const eventenddates = getEventData.map(item => item.eventenddate);
            this.eventenddate = this.eventenddate.concat(eventenddates);

            const eventplaces = getEventData.map(item => item.eventplace);
            this.eventplace = this.eventplace.concat(eventplaces);

        },

        async eventDetail(getEventDetail) {


            for (const item of getEventDetail) {
                // const eventId = item.contentid;

                //const existingData = this.events.find(event => event.contentid === eventId);
                const eventId = item.title;

                const existingData = this.events.find(event => event.title === eventId);

                if (existingData) {
                    // 이미 해당 아이디에 대한 데이터가 있다면 업데이트
                    console.log("업데이트");
                    this.$set(existingData, 'eventplace', item.eventplace);
                    this.$set(existingData, 'eventstartdate', item.eventstartdate);
                    this.$set(existingData, 'eventenddate', item.eventenddate);
                } else {
                    // 해당 아이디에 대한 데이터가 없다면 새로 추가
                    console.log("추가");

                    /*
                    this.events.push({
                        contentid: eventId,
                        eventplace: item.eventplace,
                        eventstartdate: item.eventstartdate,
                        eventenddate: item.eventenddate,
                        // ... 다른 필요한 데이터
                    });*/
                }


            }
            console.log("받아온 데이터:");
            console.log(getEventDetail);
        },

        async handleKeyword(getKeyword) {
            if (getKeyword !== '') {
                try {
                    // 키워드가 변경된 경우, 이전 데이터를 초기화
                    this.events = [];

                    const isNewKeyword = this.keyword !== getKeyword;

                    if (isNewKeyword) {
                        this.keyword = getKeyword;
                        this.currentPage = 1; // 페이지를 다시 1로 설정
                       
                        // this.hasMoreData = true; // 새로운 키워드로 검색하므로 더 많은 데이터가 있을 수 있음
                    }

                    const encodedKeyword = encodeURIComponent(this.keyword);
                    const serviceKey = 'bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D';


                    const response = await this.axios.get(
                        `http://apis.data.go.kr/B551011/KorService1/searchKeyword1?MobileOS=WIN&MobileApp=WebTest&pageNo=${this.currentPage}&contentTypeId=15&keyword=${encodedKeyword}&_type=json&serviceKey=${serviceKey}`
                    );

                    const data = response.data.response.body.items.item;

                    if (data && data.length > 0) {

                        this.events = this.events.concat(
                            data.map(event => ({
                                ...event,
                                firstimage: event.firstimage || null,
                            }))
                        );

                        // contentid만 추출
                        this.contentIds = this.events.map(event => event.contentid);

                        // 추출된 contentid를 이벤트로 보냄
                        this.$bus.$emit('contentid', this.contentIds);

                        console.log('보낸 contentid : ' + this.contentIds);
                        this.updateDisplayedEvents();
                        //getKeyword='';
                        //this.currentPage++;
                        
                    } else {
                        //this.hasMoreData = false;
                    }
                } catch (error) {
                    console.error('Error fetching event data:', error);
                }
            } else {
                // if (this.$route.path !== '/eventCrawling') {
                //     this.$router.push('/eventCrawling');
                // }
                console.log("키워드 : " + this.keyword);
            }
        },
                // 선택된 축제 장소가 구글맵에 표시되게 하기 위해 이벤트버스로 주소 전송
        handleImageClick(index) {
         
            this.$bus.$emit('sendEventLocation', index.addr1);
            
        },
        // loadMore() {

        //     if (this.hasMoreData) {
        //         this.events = [];
        //         this.contentIds = [];
        //         console.log("loadMore 메서드에서 this.keyword: ", this.keyword);
        //         console.log("버튼을 눌림" + this.currentPage);
        //         this.handleKeyword(this.keyword);
        //     }
        // },

    },
};
</script>
  
<style scoped>
.centered-container {
    max-width: 1000px;
    margin: 0 auto;
    /* 가로 가운데 정렬을 위한 스타일 */
}

li {
    list-style: none;
    /* 리스트 마커를 제거합니다. */
}
</style>
  