<template>
    <div><!--
        <template v-if="eventData.length > 0">
            <div v-for="(data, index) in eventData" :key="index">
                <h2>행사 정보 {{ index + 1 }}</h2>
                <div>
                    홈페이지: <a v-if="data.homepage" :href="data.homepage" target="_blank" v-html="data.homepage"></a>
                    <span v-else>공식 홈페이지가 없습니다.</span>
                </div>
            </div>
        </template>
        <template v-else>
            <p>이벤트 데이터가 없습니다.</p>
        </template>-->
    </div>
</template>

<script>
export default {
    name: 'event-info2',
    data() {
        return {
            eventData: [], // 여러 개의 이벤트 데이터를 저장하는 배열
            homepage:'',
        };
    },
    created() {
        this.$bus.$on('contentid', this.handleContentId);
        
    },
    methods: {
        async handleContentId(contentIds) {
            // contentIds 배열을 기반으로 데이터를 가져오는 비동기 함수 호출
            for (const contentId of contentIds) {
                await this.fetchEventData(contentId);
            }
            //console.log(this.eventData);
            //console.log(JSON.stringify(this.eventData, null, 2));
            this.$bus.$emit('sendhomepage', this.eventData);
            this.eventData = [];
        },
        async fetchEventData(contentId) {
            try {
                let serviceKey='wcWbuxyMfBGlwjkYphG6jYv3ePz%2Fzu9JAiB9u53%2FUDCO4gRXtRbL5Dfl1VaGfnn5QOhuHuPzaX2hVlQWO1wP0A%3D%3D'
                const response = await this.axios.get(
                    
                    `https://apis.data.go.kr/B551011/KorService1/detailCommon1?MobileOS=WIN&MobileApp=WebTest&defaultYN=Y&contentId=${contentId}&_type=json&serviceKey=${serviceKey}`
                     // 아빠 인증키 NNMlfJvtCxCDfwt0PP4Nebtul2vSAhEjQEhMTNHBWLDYCOppGMmuLGA9EvdoZmY7BoyIlCGw96tjiko848Zntw%3D%3D
                    // 내 인증키 bmDxI%2F3KJw7bCGRhEHG%2F3wiUzRAy5FJ0%2BUJwAXV%2BQpXSuQSs4MwYaDw8iBnhKtrsLUtyD3dxsFZ%2FVAiSTiSyFg%3D%3D
                    // 인증키3 : wcWbuxyMfBGlwjkYphG6jYv3ePz%2Fzu9JAiB9u53%2FUDCO4gRXtRbL5Dfl1VaGfnn5QOhuHuPzaX2hVlQWO1wP0A%3D%3D
                );
                const data1 = response.data.response.body.items.item;
                
                if (data1 && data1.length > 0) {
                    // 데이터가 있을 경우 배열에 추가
                    this.eventData.push({
                        homepage: data1[0].homepage,                        
                    });
                   
                } else {
                    // 데이터가 없는 경우 추가하지 않음
                    this.eventData.push({
                        homepage: "데이터없음",                  
                    });
                }
                //console.log(data1[0].homepage);
                
            } catch (error) {
                console.error('Error fetching event data:', error);
            }
        },
    },
};
</script>

